import React, {useEffect} from 'react';
import justText from '../justText';
import justNumber from '../justNumber';
import SearchableDropdown from './SearchableDropdown';

function CustomerForm({register, errors, control, watchAllFields}) {

  useEffect(() => {
    console.log(watchAllFields?.payment);
  }, [watchAllFields]);

  const deliveryDataOptions = [
    { value: "Teslim Bir", label: "Teslim Bir" },
    { value: "Teslim ki", label: "Teslim ki" },
    { value: "Teslim ", label: "Teslim " }
  ];

  const sellerDataOptions = [
    { value: "Satc Bir", label: "Satc Bir" },
    { value: "Satc ki", label: "Satc ki" },
    { value: "Satc ", label: "Satc " }
  ];

  const bankDataOptions = [
    { value: "Banka Bir", label: "Banka Bir" },
    { value: "Banka ki", label: "Banka ki" },
    { value: "Banka ", label: "Banka " }
  ];

  const paymentDataOptions = [
    { value: "Nakit", label: "Nakit" },
    { value: "Kredi Kart", label: "Kredi Kart" },
    { value: "Altta Input kacak", label: "Altta Input kacak" }
  ];

  return (
    <>
      <div className='px-5 flex justify-around w-full h-fit items-start flex-wrap'>
          <div>
            <div>ALICI ADI</div>
            
            <input {...register ('customerName', {required: 'Alc Ad Giriniz!'})} className='border border-gray-500 rounded text-black py-0.5 px-2' type="text" placeholder="Alc smi Girin" onKeyPress={(event) => justText(event)}/>
            <p className='text-red-500 font-semibold text-sm px-1 mt-1'>{errors?.customerName?.message}</p>
          </div>

          <div>
            <div>ALICI CAR KOD</div>
            <input {...register ('customerCode', {required: 'Alc Cari Kodu Giriniz!'})}  className='border border-gray-500 rounded text-black py-0.5 px-2' type="text" placeholder='Alc Cari Kod' onKeyPress={(event) => justText(event)}/>
            <p className='text-red-500 font-semibold text-sm px-1 mt-1'>{errors?.customerCode?.message}</p>
          </div>


          <div>
            <div>TESLM</div>
            <SearchableDropdown register={register} control={control} options={deliveryDataOptions} inputName={`delivery`} errorMessage='Teslimat Seiniz!' placeholder='Seiniz...' />
            <p className='text-red-500 font-semibold text-sm px-1 mt-1'>{errors?.delivery?.message}</p>
          </div>

          <div>
            <div>TESLM N ZEL KOD</div>
            <input {...register ('deliveryCode', {required: 'Teslimat in zel Kod Giriniz!'})} className='border border-gray-500 rounded text-black py-0.5 px-2' type="text" placeholder='Teslim in zel Kod' onKeyPress={(event) => justText(event)}/>
            <p className='text-red-500 font-semibold text-sm px-1 mt-1'>{errors?.deliveryCode?.message}</p>
          </div>

          <div>
            <div>SATICI</div>
            <SearchableDropdown register={register} control={control} options={sellerDataOptions} inputName={`seller`} errorMessage='Satc Seiniz!' placeholder='Seiniz...' />
            <p className='text-red-500 font-semibold text-sm px-1 mt-1'>{errors?.seller?.message}</p>
          </div>

          <div>
            <div>BANKA</div>
            <SearchableDropdown register={register} control={control} options={bankDataOptions} inputName={`bank`} errorMessage='Banka Seiniz!' placeholder='Seiniz...' />
            <p className='text-red-500 font-semibold text-sm px-1 mt-1'>{errors?.bank?.message}</p>
          </div>


          <div>
            <div>DEME</div>
            <SearchableDropdown register={register} control={control} options={paymentDataOptions} inputName={`payment`} errorMessage='deme Yntemi Seiniz!' placeholder='Seiniz...' />
            { watchAllFields?.payment === 'Altta Input kacak' &&
            <>
              <input {...register ('paymentSubInput', {required: 'paymentSubInput Doldurunuz!'})} className='border border-gray-500 rounded text-black py-0.5 px-2 mt-1 w-full' type="text" placeholder='Teslim in zel Kod' onKeyPress={(event) => justText(event)}/>
              <p className='text-red-500 font-semibold text-sm px-1 mt-1'>{errors?.paymentSubInput?.message}</p>
            </>
              
            }
            <p className='text-red-500 font-semibold text-sm px-1 mt-1'>{errors?.payment?.message}</p>
          </div>

          <div>
            <div>FATURA NO.</div>
            <input {...register ('billNo', {required: 'Fatura No. Giriniz!'})} className='border border-gray-500 rounded text-black py-0.5 px-2' type="number" placeholder='Fatura No' onKeyPress={(event) => justNumber(event)}/>
            <p className='text-red-500 font-semibold text-sm px-2 mt-1'>{errors?.billNo?.message}</p>
          </div>
        



      </div>
    </>
  )
}

export default CustomerForm;