import React, { useState } from 'react'
import Multiselect from 'multiselect-react-dropdown';
// import justText from '../justText';
import SearchableDropdown from './SearchableDropdown';

function Selections({index, register, errors, update, append, fields, contentRemove, control, clearErrors}) {
  
  const [contentNames] = useState(['NEVRESM', 'ARAF', 'STD. YASTIK KILIFI', 'OXF. YASTIK KILIFI', 'KIRLENT KILIFI']);
  
  const brandDataOptions = [
    { value: "Marka Bir", label: "Marka Bir" },
    { value: "Marka ki", label: "Marka ki" },
    { value: "Marka ", label: "Marka " }
  ];

  const sizeDataOptions = [
    { value: "Beden Bir", label: "Beden Bir" },
    { value: "Beden ki", label: "Beden ki" },
    { value: "Beden ", label: "Beden " }
  ];

  const productTypeDataOptions = [
    { value: "rn Cinsi Bir", label: "rn Cinsi Bir" },
    { value: "rn Cinsi ki", label: "rn Cinsi ki" },
    { value: "rn Cinsi ", label: "rn Cinsi " }
  ];

  const packageTypeDataOptions = [
    { value: "Ambalaj Bir", label: "Ambalaj Bir" },
    { value: "Ambalaj ki", label: "Ambalaj ki" },
    { value: "Ambalaj ", label: "Ambalaj " }
  ];
  


  return (
    <div className='text-black'>

      {/* Marka / Brand */}
      <div className='my-4'>
        <div>Marka:</div>
        <SearchableDropdown register={register} control={control} options={brandDataOptions} inputName={`products[${index}].brand`} errorMessage='Marka Seiniz!' placeholder='Marka Se' />
        <p className='text-red-500 font-semibold text-sm mt-1'>{errors.products?.[index]?.brand?.message}</p>
      </div>

      {/* Beden / Size */}
      <div className='my-4'>
        <div>Beden:</div>
        <SearchableDropdown register={register} control={control} options={sizeDataOptions} inputName={`products[${index}].size`} errorMessage='Boyut Seiniz!' placeholder='Boyut Se' />
        <p className='text-red-500 font-semibold text-sm mt-1'>{errors.products?.[index]?.size?.message}</p>
      </div> 
      
      {/* rn Cinsi / Product Type */}
      <div className='my-4'>
        <div>rn Cinsi:</div>
        <SearchableDropdown register={register} control={control} options={productTypeDataOptions} inputName={`products[${index}].productType`} errorMessage='rn Cinsi Seiniz!' placeholder='rn Cinsi Se' />
        <p className='text-red-500 font-semibold text-sm mt-1'>{errors.products?.[index]?.productType?.message}</p>
      </div>

      {/* ierikler / Contents */}
      <div className='my-4'>
        <div>erikler:</div>
        <div className='bg-white'>
          <Multiselect
            isObject={false}
            displayValue='value'
            className='max-w-64'
            style={{
              searchBox: {
                border: '1px solid #374151',
                // padding: '12px'
              }
            }}
            onKeyPressFn={function noRefCheck(){}}
            onRemove={(selectedList, removedItem) => {
              contentRemove(fields.map((item) => item.contentName).indexOf(removedItem))
            }}
            onSearch={function noRefCheck(){}}
            onSelect={(selectedList, selectedItem) => {
              append({
                contentName: selectedItem,
                contentPiece: '',
                readySizeSelected: false,
                contentReadySize: '',
                contentHeight: '',
                contentWidth: '',
                weightSelected: false,
                contentStitch: ''
              })
              if(errors?.products?.[index]?.content?.type === 'custom'){
                clearErrors(`products[${index}].content`);
              }
            }}
            options={contentNames}
            showCheckbox
            placeholder="erik Se"
            avoidHighlightFirstOption={true}
          />
        </div>
      </div>

      {/* Ambalaj / Package */}
      <div className='my-4'>
        <div>Ambalaj:</div>
        <SearchableDropdown register={register} control={control} options={packageTypeDataOptions} inputName={`products[${index}].package`} errorMessage='Ambalaj Seiniz!' placeholder='Ambalaj Se' />
        <p className='text-red-500 font-semibold text-sm mt-1'>{errors.products?.[index]?.package?.message}</p>
      </div>
    </div>
  );
}

export default Selections;